





























import {
    Component,
    Prop,
    Vue,
} from 'vue-property-decorator';

@Component({})
export default class HeaderUser extends Vue {

    private menuCommand: any = {
        logout:  this.logout,
    };

    private handleCommand(e: any) {
        this.menuCommand[e.key]();
    }

    /**
     * 退出登录，需要做其他事情
     */
    private logout() {
        this.$router.push('/passport/login');
    }
}
